.watermark-container {
  width: 1000px;
  height: 350px;
  background-color: #f3f4f6; /* This is a light gray color, similar to bg-slate-500 */
  margin-top: 200px;
  position: relative; /* This is needed to position the watermark image */
 }
 
 .watermark-container::after {
  content: "";
  background-image: url('https://i.postimg.cc/q7FYjvQX/bgQr.jpg'); /* Path to your watermark image */
  background-repeat: no-repeat;
  background-position: center; /* Adjust as needed */
  background-size: 100% auto; /* Adjust as needed */
  opacity: 0.5; /* Adjust the opacity as needed */
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: 1;
   /* Ensures the watermark is behind the content */
 }

 